@import url(https://fonts.googleapis.com/css2?family=Paytone+One&family=Poppins&family=Raleway&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* font-family: 'Raleway', sans-serif; */
/* font-family: 'Paytone One', sans-serif; */
/* font-family: 'Poppins', sans-serif; */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 14px;
  font-family: 'Poppins', sans-serif;
  color: rgb(61, 61, 60);
}
.main-box {
  width: 800px;
  margin: 20px auto;
  position: relative;
}
.major-div {
  width: 800px;
  height: 600px;
  padding: 20px;
  position: absolute;
  display: flex;
  justify-content: center;
  align-content: center;
  flex-flow: row wrap;
}
.game-banner {
  width: 600px;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
}
.game-banner div:first-child {
  width: 16%;
  overflow: hidden;
}
.game-banner img {
  width: 100%;
  object-fit: contain;
}
.game-banner div {
  width: 80%;
  color: rgb(240, 135, 0);
  font-size: 350%;
  text-align: center;
  font-family: 'Paytone One', sans-serif;
}
.instructions p {
  width: 55%;
  text-align: center;
  margin-top: 10px;
  font-size: 14px;
}
.instructions p:nth-child(2) {
  color: rgb(0, 187, 0);
  font-weight: 900;
  font-size: 20px;
  margin-top: 0;
}
.instructions > div:last-child {
  width: 60%;
  text-align: center;
  margin-top: 20px;
}
.instructions button,
.gameover button {
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 18px;
  font-weight: 900;
  padding: 5px 20px;
  background-color: white;
  color: rgb(226, 71, 60);
  border: 3px solid rgb(226, 71, 60);
  border-radius: 20px;
}
.instructions button:hover,
.gameover button:hover {
  background-color: rgb(226, 71, 60);
  color: white;
}
.game-div > div:nth-child(2) {
  width: 35%;
  display: flex;
  justify-content: center;
}
.game-div > div:nth-child(2) div {
  width: 100%;
  text-align: center;
}
.stat-item {
  width: 100%;
}
.stat-item div:first-child {
  color: rgb(0, 187, 0);
  font-size: 15px;
  text-transform: uppercase;
  letter-spacing: 1px;
  height: 15px;
}
.stat-item div:last-child {
  font-size: 30px;
  color: rgb(226, 71, 60);
}
.score-box {
  border-right: 2px solid rgb(0, 187, 0);
}
#fruits-display {
  width: 100%;
  margin-top: 20px;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
  align-items: center;
}
.fruit-order-div {
  width: 200px;
  overflow: hidden;
}
.fruit-order-div img {
  width: 100%;
  object-fit: contain;
}
.fruit-btns {
  width: 58%;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
}
.fruit-btns button {
  width: 80px;
  height: 80px;
  overflow: hidden;
  padding: 5px;
  margin: 3px 0;
  background-color: rgb(255, 254, 185);
  border: 3px solid rgb(120, 226, 120);
  border-radius: 10px;
}
.fruit-btns button:hover {
  border: 3px solid rgb(226, 71, 60);
}
.fruit-btns button:active {
  border: 3px solid rgb(61, 61, 60);
}
.fruit-btns img {
  width: 100%;
  object-fit: cover;
}
.game-over-statements {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.game-over-statements div {
  text-align: center;
}
.game-over-statements div:first-child {
  font-size: 24px;
  color: rgb(226, 71, 60);
  font-weight: 900;
  width: 100%;
}
.game-over-statements div:nth-child(2) {
  width: 30%;
}
.game-over-statements div:nth-child(2) div:first-child {
  color: rgb(0, 187, 0);
  font-size: 16px;
  font-weight: 800;
  height: 22px;
  margin-top: 5px;
  padding-top: 5px;
  border-top: 1px solid rgb(0, 187, 0);
  width: 100%;
}
.game-over-statements div:nth-child(2) div:last-child {
  color: rgb(240, 135, 0);
  font-size: 30px;
  font-weight: 800;
  border-bottom: 1px solid rgb(0, 187, 0);
  margin-bottom: 10px;
  width: 100%;
}
.game-over-statements div:nth-child(3) {
  width: 100%;
}

